.header-main-container {
  padding: 0.3rem 1.5rem;
}

.header-links-desktop {
  display: none;
}

.header-links-mobile {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 3rem;
}

.headline-text {
  font-weight: 700;
}

@media (min-width: 900px) {
  .header-links-desktop {
    display: block;
  }

  .header-links-mobile {
    display: none;
  }
}

@media (min-width: 900px) {
  .card {
    text-align: center;
    padding-top: 1rem;
  }
  .main-pic {
    max-width: 70%;
    margin: 0 auto;
  }
}

a,
.mainText a {
  color: #0a4580;
  background: none;
}

a:hover {
  color: #2f46ff;
}

.mainShadow {
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.mainPaddingAllSides {
  padding: 1rem;
}

.mainContent {
  font-family: 'Crimson Text', serif;
}

.mainHeading {
  font-family: 'Tangerine', cursive;
}

.mainText {
  background: -webkit-linear-gradient(right, #edce8e, #b09053);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

